import { useCallback, useState } from 'react';

/**
 * 
 */
const useHistory = () => {
  const [history, setHistory] = useState([]);

  // 
  const appendHistory = useCallback(step => setHistory(prev => [...prev, step]), []);

  // 
  const updateHistory = useCallback(nextHistory => setHistory(nextHistory), []);

  return { history, appendHistory, updateHistory };
};

export default useHistory;
